import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { MainNavComponent } from './main.component';

import { MatButtonModule } from '@angular/material/button';
import { MatCardModule } from '@angular/material/card';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatProgressBarModule } from '@angular/material/progress-bar';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { MatIconModule } from '@angular/material/icon';
import { MatListModule } from '@angular/material/list';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatExpansionModule } from '@angular/material/expansion';

const routes: Routes = [
  {
    path: '',
    component: MainNavComponent,
    children: [
      {
        path: '',
        redirectTo: 'thong-tin',
        pathMatch: 'full',
      },
      {
        path: 'muon-thiet-bi',
        loadChildren: () =>
          import('./thiet-bi/muon-thiet-bi/muon-thiet-bi.module').then(
            (m) => m.MuonThietBiModule
          ),
      },
      {
        path: 'yeu-cau-su-dung-thiet-bi',
        loadChildren: () =>
          import('./thiet-bi/yeu-cau/yeu-cau.module').then(
            (m) => m.YeuCauModule
          ),
      },
      {
        path: 'lich-su-su-dung-thiet-bi',
        loadChildren: () =>
          import(
            './thiet-bi/lich-su/lich-su-su-dung-thiet-bi.module'
          ).then((m) => m.LichSuSuDungThietBiModule),
      },
      {
        path: 'thong-tin',
        loadChildren: () =>
          import('./thong-tin-co-ban/thong-tin-co-ban.module').then(
            (m) => m.ThongTinCoBanModule
          ),
      },
      {
        path: 'lich-su-su-dung-phong-thi-nghiem',
        loadChildren: () =>
          import('./su-dung-ptn/lich-su-su-dung-ptn/lich-su-su-dung-ptn.module').then(
            (m) => m.LichSuSuDungPTNModule
          ),
      },
      {
        path: 'yeu-cau-su-dung-phong-thi-nghiem',
        loadChildren: () =>
          import('./su-dung-ptn/yeu-cau/yeu-cau.module').then(
            (m) => m.YeuCauSuDungPTNModule
          ),
      },
      {
        path: 'lien-he',
        loadChildren: () =>
          import('./lien-he-client/lien-he-client.module').then(
            (m) => m.LienHeClientModule
          ),
      },
    ],
  },
];
@NgModule({
  imports: [
    RouterModule.forChild(routes),
    CommonModule,
    MatProgressBarModule,
    MatCardModule,
    MatFormFieldModule,
    FormsModule,
    MatButtonModule,
    MatSidenavModule,
    MatToolbarModule,
    MatListModule,
    MatIconModule,
    MatExpansionModule
  ],
  declarations: [MainNavComponent],
})
export class MainNavModule {}
